"use client";

import { useEffect, useRef, useState } from "react";
import { TelemetryOverlay } from "./TelemetryOverlay";

interface BoundaryPoint {
  distance: number;
  x: number;
  z: number;
}

interface TrackBoundaries {
  track: {
    id: number;
    name: string;
  };
  left: BoundaryPoint[];
  right: BoundaryPoint[];
  originalPointCount: {
    left: number;
    right: number;
  };
  processedPointCount: {
    left: number;
    right: number;
  };
}

interface TrackMapWithBoundariesProps {
  lapData: any;
  corners?: any[];
  currentIndex?: number;
  trackName?: string;
  referenceLapData?: any; // For comparison
}

export function TrackMapWithBoundaries({
  lapData,
  corners = [],
  currentIndex = 0,
  trackName = "sakhir-(bahrain)",
  referenceLapData,
}: TrackMapWithBoundariesProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [boundaries, setBoundaries] = useState<TrackBoundaries | null>(null);
  const [showBoundaries, setShowBoundaries] = useState(true);
  const [showRacingLine, setShowRacingLine] = useState(true);
  const [showBrakingZones, setShowBrakingZones] = useState(true);
  const [showCornerMarkers, setShowCornerMarkers] = useState(true);
  const [showImprovements, setShowImprovements] = useState(true);
  const [tooltip, setTooltip] = useState<{ x: number; y: number; text: string } | null>(null);
  const [hoveredImprovement, setHoveredImprovement] = useState<number | null>(null);

  // Load boundaries
  useEffect(() => {
    fetch(`/api/boundaries/${trackName}`)
      .then(res => res.json())
      .then(data => {
        if (data.error) {
          console.warn('Boundaries not available:', data.error);
          setBoundaries(null);
        } else {
          setBoundaries(data);
        }
      })
      .catch(err => {
        console.error('Failed to load boundaries:', err);
        setBoundaries(null);
      });
  }, [trackName]);

  useEffect(() => {
    if (!canvasRef.current || !lapData?.telemetry) {
      return;
    }

    const telemetry = lapData.telemetry;
    if (telemetry.length === 0) {
      return;
    }

    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const dpr = window.devicePixelRatio || 1;
    const rect = canvas.getBoundingClientRect();
    canvas.width = rect.width * dpr;
    canvas.height = rect.height * dpr;
    ctx.scale(dpr, dpr);

    const width = rect.width;
    const height = rect.height;

    // Get current car position for camera follow
    const currentPoint = telemetry[Math.floor(currentIndex)];
    const centerX = currentPoint ? currentPoint.y : 0;
    const centerY = currentPoint ? -currentPoint.x : 0;

    // Camera zoom level (higher = more zoomed in)
    const zoomScale = 1.0;

    // View range around car (in meters)
    const viewRange = 150;

    const minX = centerX - viewRange;
    const maxX = centerX + viewRange;
    const minY = centerY - viewRange;
    const maxY = centerY + viewRange;

    const rangeX = maxX - minX;
    const rangeY = maxY - minY;
    const scale = Math.min(width / rangeX, height / rangeY) * zoomScale;

    const toScreenX = (x: number, y: number) => {
      const rx = y;
      return (width / 2) + (rx - centerX) * scale;
    };
    const toScreenY = (x: number, y: number) => {
      const ry = -x;
      return (height / 2) + (ry - centerY) * scale;
    };

    // Clear background
    ctx.fillStyle = "#0a0a0a";
    ctx.fillRect(0, 0, width, height);

    // Draw track boundaries
    if (boundaries && showBoundaries) {
      // Draw track surface (filled area between boundaries)
      ctx.fillStyle = "#1a1a1a";
      ctx.beginPath();

      // Start at left boundary first point
      const leftStart = boundaries.left[0];
      ctx.moveTo(toScreenX(leftStart.x, leftStart.z), toScreenY(leftStart.x, leftStart.z));

      // Draw to right boundary first point (connect start line)
      const rightStart = boundaries.right[0];
      ctx.lineTo(toScreenX(rightStart.x, rightStart.z), toScreenY(rightStart.x, rightStart.z));

      // Draw along right boundary
      boundaries.right.forEach((p, i) => {
        if (i === 0) return; // Skip first point (already there)
        const x = toScreenX(p.x, p.z);
        const y = toScreenY(p.x, p.z);
        ctx.lineTo(x, y);
      });

      // Connect to left boundary last point (finish line)
      const leftEnd = boundaries.left[boundaries.left.length - 1];
      ctx.lineTo(toScreenX(leftEnd.x, leftEnd.z), toScreenY(leftEnd.x, leftEnd.z));

      // Draw back along left boundary (reverse)
      for (let i = boundaries.left.length - 2; i >= 0; i--) {
        const p = boundaries.left[i];
        const x = toScreenX(p.x, p.z);
        const y = toScreenY(p.x, p.z);
        ctx.lineTo(x, y);
      }

      ctx.closePath();
      ctx.fill();

      // Draw boundary lines
      // Left boundary
      ctx.strokeStyle = "rgba(255, 68, 68, 0.6)";
      ctx.lineWidth = 2;
      ctx.beginPath();
      boundaries.left.forEach((p, i) => {
        const x = toScreenX(p.x, p.z);
        const y = toScreenY(p.x, p.z);
        if (i === 0) ctx.moveTo(x, y);
        else ctx.lineTo(x, y);
      });
      ctx.stroke();

      // Right boundary
      ctx.strokeStyle = "rgba(68, 136, 255, 0.6)";
      ctx.lineWidth = 2;
      ctx.beginPath();
      boundaries.right.forEach((p, i) => {
        const x = toScreenX(p.x, p.z);
        const y = toScreenY(p.x, p.z);
        if (i === 0) ctx.moveTo(x, y);
        else ctx.lineTo(x, y);
      });
      ctx.stroke();

      // Draw start/finish line
      if (boundaries.left.length > 0 && boundaries.right.length > 0) {
        const leftStart = boundaries.left[0];
        const rightStart = boundaries.right[0];
        ctx.strokeStyle = "rgba(0, 255, 136, 0.8)";
        ctx.lineWidth = 3;
        ctx.setLineDash([5, 5]);
        ctx.beginPath();
        ctx.moveTo(toScreenX(leftStart.x, leftStart.z), toScreenY(leftStart.x, leftStart.z));
        ctx.lineTo(toScreenX(rightStart.x, rightStart.z), toScreenY(rightStart.x, rightStart.z));
        ctx.stroke();
        ctx.setLineDash([]);
      }
    }

    // Draw braking zones
    if (showBrakingZones) {
      corners.forEach((corner: any) => {
        if (corner.brakingZone) {
          const bz = corner.brakingZone;
          const startIdx = telemetry.findIndex((p: any) => p.distance >= bz.entryDistance);
          const endIdx = telemetry.findIndex((p: any) => p.distance >= bz.exitDistance);

          if (startIdx >= 0 && endIdx >= 0) {
            ctx.strokeStyle = "rgba(255, 60, 60, 0.7)";
            ctx.lineWidth = 8;
            ctx.lineCap = "round";
            ctx.lineJoin = "round";

            ctx.beginPath();
            for (let i = startIdx; i <= endIdx; i++) {
              const p = telemetry[i];
              const x = toScreenX(p.x, p.y);
              const y = toScreenY(p.x, p.y);
              if (i === startIdx) ctx.moveTo(x, y);
              else ctx.lineTo(x, y);
            }
            ctx.stroke();
          }
        }
      });
    }

    // Draw racing line
    if (showRacingLine) {
      ctx.lineWidth = 3;
      ctx.strokeStyle = "#ffff00";
      ctx.lineCap = "round";
      ctx.lineJoin = "round";

      ctx.beginPath();
      telemetry.forEach((p: any, i: number) => {
        const x = toScreenX(p.x, p.y);
        const y = toScreenY(p.x, p.y);
        if (i === 0) ctx.moveTo(x, y);
        else ctx.lineTo(x, y);
      });
      ctx.stroke();
    }

    // Draw corner markers
    if (showCornerMarkers) {
      corners.forEach((corner: any, idx: number) => {
        const apexPoint = telemetry.find(
          (p: any) => Math.abs(p.distance - corner.apexDistance) < 5
        );

        if (apexPoint) {
          const x = toScreenX(apexPoint.x, apexPoint.y);
          const y = toScreenY(apexPoint.x, apexPoint.y);

          ctx.fillStyle = "#000";
          ctx.beginPath();
          ctx.arc(x, y, 16, 0, Math.PI * 2);
          ctx.fill();

          ctx.fillStyle = "#fff";
          ctx.beginPath();
          ctx.arc(x, y, 14, 0, Math.PI * 2);
          ctx.fill();

          ctx.fillStyle = "#000";
          ctx.font = "bold 11px sans-serif";
          ctx.textAlign = "center";
          ctx.textBaseline = "middle";
          ctx.fillText(`${idx + 1}`, x, y);
        }
      });
    }

    // Draw improvement opportunity markers (simple markers, details in side panel)
    if (showImprovements && referenceLapData) {
      corners.forEach((corner: any, idx: number) => {
        if (corner.brakingZone) {
          const bz = corner.brakingZone;
          const markerPoint = telemetry.find((p: any) =>
            Math.abs(p.distance - bz.entryDistance) < 5
          );

          if (markerPoint) {
            const mx = toScreenX(markerPoint.x, markerPoint.y);
            const my = toScreenY(markerPoint.x, markerPoint.y);

            // Simple marker dot
            ctx.fillStyle = "#fbbf24";
            ctx.beginPath();
            ctx.arc(mx, my, 6, 0, Math.PI * 2);
            ctx.fill();

            // Small number
            ctx.fillStyle = "#000";
            ctx.font = "bold 10px Arial";
            ctx.textAlign = "center";
            ctx.textBaseline = "middle";
            ctx.fillText(`${idx + 1}`, mx, my);
          }
        }
      });
    }

    // Draw current position as a clean dot (reuse currentPoint from above)
    if (currentPoint) {
      const cx = toScreenX(currentPoint.x, currentPoint.y);
      const cy = toScreenY(currentPoint.x, currentPoint.y);

      // Outer ring
      ctx.fillStyle = "#3b82f6";
      ctx.beginPath();
      ctx.arc(cx, cy, 8, 0, Math.PI * 2);
      ctx.fill();

      // Inner dot
      ctx.fillStyle = "#ffffff";
      ctx.beginPath();
      ctx.arc(cx, cy, 4, 0, Math.PI * 2);
      ctx.fill();
    }
  }, [lapData, corners, currentIndex, boundaries, showBoundaries, showRacingLine, showBrakingZones, showCornerMarkers, showImprovements]);

  const currentTelemetryPoint = lapData?.telemetry?.[Math.floor(currentIndex)];

  return (
    <div className="relative rounded-lg border border-zinc-800 bg-zinc-900/50 mx-auto" style={{ maxWidth: "1200px" }}>
      {/* Telemetry Overlay */}
      <TelemetryOverlay telemetryPoint={currentTelemetryPoint} />

      {/* Toggle controls - moved to bottom right corner */}
      <div className="absolute bottom-20 right-2 z-20 flex flex-col gap-1 pointer-events-auto">
        <button
          onClick={() => setShowBoundaries(!showBoundaries)}
          className={`px-2 py-1 text-[10px] uppercase tracking-wide transition-colors ${
            showBoundaries
              ? 'bg-black/90 text-white border border-zinc-700'
              : 'bg-black/60 text-zinc-500 border border-zinc-800 hover:bg-black/80'
          }`}
        >
          Boundaries
        </button>
        <button
          onClick={() => setShowRacingLine(!showRacingLine)}
          className={`px-2 py-1 text-[10px] uppercase tracking-wide transition-colors ${
            showRacingLine
              ? 'bg-black/90 text-white border border-zinc-700'
              : 'bg-black/60 text-zinc-500 border border-zinc-800 hover:bg-black/80'
          }`}
        >
          Line
        </button>
        <button
          onClick={() => setShowBrakingZones(!showBrakingZones)}
          className={`px-2 py-1 text-[10px] uppercase tracking-wide transition-colors ${
            showBrakingZones
              ? 'bg-black/90 text-white border border-zinc-700'
              : 'bg-black/60 text-zinc-500 border border-zinc-800 hover:bg-black/80'
          }`}
        >
          Braking
        </button>
        <button
          onClick={() => setShowCornerMarkers(!showCornerMarkers)}
          className={`px-2 py-1 text-[10px] uppercase tracking-wide transition-colors ${
            showCornerMarkers
              ? 'bg-black/90 text-white border border-zinc-700'
              : 'bg-black/60 text-zinc-500 border border-zinc-800 hover:bg-black/80'
          }`}
        >
          Corners
        </button>
        <button
          onClick={() => setShowImprovements(!showImprovements)}
          className={`px-2 py-1 text-[10px] uppercase tracking-wide transition-colors ${
            showImprovements
              ? 'bg-black/90 text-yellow-400 border border-yellow-600'
              : 'bg-black/60 text-zinc-500 border border-zinc-800 hover:bg-black/80'
          }`}
        >
          Tips
        </button>
      </div>

      <canvas
        ref={canvasRef}
        className="cursor-crosshair bg-black mx-auto"
        style={{ height: "650px", width: "1200px", maxWidth: "100%" }}
      />

      {tooltip && (
        <div
          className="pointer-events-none fixed z-50 rounded bg-black px-3 py-2 text-sm text-white shadow-lg border border-zinc-700"
          style={{
            left: tooltip.x + 10,
            top: tooltip.y + 10,
          }}
        >
          {tooltip.text}
        </div>
      )}
    </div>
  );
}
