"use client";

import { useEffect, useState } from "react";

interface ImprovementInsightsPanelProps {
  currentLapNumber: number;
  referenceLapNumber?: number;
}

interface Insight {
  corner: number;
  type: string;
  title: string;
  description: string;
  potentialGain: string;
  confidence: 'high' | 'medium' | 'low';
}

export function ImprovementInsightsPanel({
  currentLapNumber,
  referenceLapNumber
}: ImprovementInsightsPanelProps) {
  const [insights, setInsights] = useState<Insight[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!referenceLapNumber) return;

    setLoading(true);

    // Fetch all analysis types
    Promise.all([
      fetch(`/api/analysis/compare?lapA=${currentLapNumber}&lapB=${referenceLapNumber}&type=speed`).then(r => r.json()),
      fetch(`/api/analysis/compare?lapA=${currentLapNumber}&lapB=${referenceLapNumber}&type=gforce`).then(r => r.json()),
    ])
      .then(([speedData, gforceData]) => {
        const allInsights: Insight[] = [];

        // Speed-based insights
        if (speedData?.comparison?.cornerComparison) {
          speedData.comparison.cornerComparison.forEach((corner: any, idx: number) => {
            // Check for late braking opportunities
            if (corner.delta.entry < -5) { // 5+ km/h slower on entry
              allInsights.push({
                corner: idx + 1,
                type: 'braking',
                title: 'Late Braking Opportunity',
                description: `You're entering ${Math.abs(corner.delta.entry).toFixed(1)} km/h slower. Brake later to carry more speed into the corner.`,
                potentialGain: `~${Math.abs(corner.delta.time).toFixed(2)}s`,
                confidence: Math.abs(corner.delta.entry) > 10 ? 'high' : 'medium',
              });
            }

            // Check for apex speed
            if (corner.delta.apex < -5) {
              allInsights.push({
                corner: idx + 1,
                type: 'apex',
                title: 'Slow Through Apex',
                description: `Apex speed is ${Math.abs(corner.delta.apex).toFixed(1)} km/h slower. Try a later turn-in or smoother steering input.`,
                potentialGain: `~${Math.abs(corner.delta.time).toFixed(2)}s`,
                confidence: Math.abs(corner.delta.apex) > 8 ? 'high' : 'medium',
              });
            }

            // Check for exit speed
            if (corner.delta.exit < -5) {
              allInsights.push({
                corner: idx + 1,
                type: 'exit',
                title: 'Poor Corner Exit',
                description: `Exit speed is ${Math.abs(corner.delta.exit).toFixed(1)} km/h slower. Apply throttle earlier and more progressively.`,
                potentialGain: `~${Math.abs(corner.delta.time).toFixed(2)}s`,
                confidence: Math.abs(corner.delta.exit) > 10 ? 'high' : 'medium',
              });
            }
          });
        }

        // G-Force based insights
        if (gforceData?.comparison?.insights) {
          gforceData.comparison.insights.forEach((insight: any) => {
            allInsights.push({
              corner: insight.corner,
              type: 'gforce',
              title: insight.insight.split(':')[0],
              description: insight.recommendation,
              potentialGain: 'Varies',
              confidence: insight.confidence,
            });
          });
        }

        // Sort by corner number and filter duplicates
        const uniqueInsights = allInsights
          .sort((a, b) => a.corner - b.corner)
          .filter((insight, index, self) =>
            index === self.findIndex((t) => t.corner === insight.corner && t.type === insight.type)
          );

        setInsights(uniqueInsights);
        setLoading(false);
      })
      .catch((err) => {
        console.error('Failed to load insights:', err);
        setLoading(false);
      });
  }, [currentLapNumber, referenceLapNumber]);

  if (!referenceLapNumber) {
    return (
      <div className="bg-black border border-zinc-800 p-4 h-full">
        <div className="text-center text-zinc-500 py-8">
          <div className="text-2xl mb-2">ðŸ’¡</div>
          <div className="text-sm">Select a reference lap in comparison mode to see improvement tips</div>
        </div>
      </div>
    );
  }

  const getConfidenceColor = (confidence: string) => {
    switch (confidence) {
      case 'high': return 'bg-green-500/20 text-green-400 border-green-500';
      case 'medium': return 'bg-yellow-500/20 text-yellow-400 border-yellow-500';
      case 'low': return 'bg-orange-500/20 text-orange-400 border-orange-500';
      default: return 'bg-zinc-500/20 text-zinc-400 border-zinc-500';
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'braking': return 'ðŸ”´';
      case 'apex': return 'ðŸŽ¯';
      case 'exit': return 'ðŸš€';
      case 'gforce': return 'âš¡';
      default: return 'ðŸ’¡';
    }
  };

  return (
    <div className="bg-black border border-zinc-800 h-full flex flex-col">
      <div className="border-b border-zinc-800 px-4 py-3">
        <h3 className="text-sm font-semibold text-white uppercase tracking-wider">
          Improvement Tips
        </h3>
        <p className="text-xs text-zinc-500 mt-1">
          Based on comparison with Lap {referenceLapNumber}
        </p>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-3">
        {loading ? (
          <div className="text-center text-zinc-500 py-8">
            <div className="text-sm">Analyzing...</div>
          </div>
        ) : insights.length === 0 ? (
          <div className="text-center text-zinc-500 py-8">
            <div className="text-2xl mb-2">âœ…</div>
            <div className="text-sm">No major improvements found!</div>
            <div className="text-xs mt-1">You're matching the reference lap well</div>
          </div>
        ) : (
          insights.map((insight, idx) => (
            <div key={idx} className="bg-zinc-900/50 border border-zinc-800 p-3">
              <div className="flex items-start gap-3">
                <div className="text-xl">{getTypeIcon(insight.type)}</div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center gap-2 mb-1">
                    <span className="text-white font-medium text-sm">Turn {insight.corner}</span>
                    <span className={`text-[10px] px-2 py-0.5 border uppercase tracking-wider ${getConfidenceColor(insight.confidence)}`}>
                      {insight.confidence}
                    </span>
                  </div>
                  <div className="text-xs font-semibold text-cyan-400 mb-1">{insight.title}</div>
                  <div className="text-xs text-zinc-300">{insight.description}</div>
                  <div className="text-[10px] text-zinc-500 mt-2">
                    Potential gain: <span className="text-green-400 font-mono">{insight.potentialGain}</span>
                  </div>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
